@font-face {
    font-family: 'Russo One';
    src: url("assets/fonts/RussoOne-Regular.ttf") format("truetype");
}

@font-face {
    font-family: 'Saira Condensed';
    src: url("assets/fonts/SairaCondensed-Regular.ttf") format("truetype");
}


body {
    background-color: rgb(20, 20, 20);
    color: white;
    margin: 20px;
    font-family: 'Saira Condensed';
}

p {
    margin-top: 0.5em;
    margin-bottom: 0.5em;
}

h1 {
    color: red;
    display: inline;
    font-family: 'Russo One';
    font-size: 32pt;
    margin: 5px;
    text-shadow:
        0 0 3px rgba(200, 0, 0, 0.3),
        0 0 5px rgba(200, 0, 0, 0.25),
        0 0 10px rgba(200, 0, 0, 0.2),
        0 0 42px #f00,
        0 0 82px #f00,
        0 0 92px #f00,
        0 0 102px #f00,
        0 0 151px #f00;
    user-select: none;
}

h2 {
    color: red;
    display: inline;
    font-family: 'Russo One';
    text-shadow:
        0 0 3px rgba(200, 0, 0, 0.3),
        0 0 5px rgba(200, 0, 0, 0.25),
        0 0 10px rgba(200, 0, 0, 0.2),
        0 0 42px #f00,
        0 0 82px #f00;
    user-select: none;
}

h3 {
    color: red;
    display: inline;
    font-family: 'Russo One';
    text-shadow:
        0 0 3px rgba(200, 0, 0, 0.3),
        0 0 5px rgba(200, 0, 0, 0.25),
        0 0 10px rgba(200, 0, 0, 0.2),
        0 0 20px rgba(200, 0, 0, 0.5),
        0 0 40px rgba(200, 0, 0, 0.5);
    user-select: none;
}

hr {
    border-color: red;
    box-shadow:
        0 0 1px red,
        0 0 3px red,
        0 0 5px red;
    margin-top: 1.5em;
    margin-bottom: 1.5em;
}
